<script>
export default {
  name:'XQTC',
  props:['type','xid'],
  data(){
    return{
      xqData0:{},
      xqData1:{},
      xqData2:{},
      xqData3:{},
      xqData4:{},
      xqData6:{},
      page:this.type,
      id:'',
    }
  },
  mounted(){
    this.initData(this.page)

  },
  watch:{
    type:function(val){
      this.page=val;
      this.initData(val)
    },
    xid:function(val){
      this.id=val
    },

  },
  methods:{
    initData(type){
      switch (type) {
        case 0:
          this.getData0();
          break;
        case 1:
          this.getData1();
          break;
        case 2:
          this.getData2();
          break;
        case 3:
          this.getData3();
          break;
        case 4:
          this.getData4();
          break;
        case 5:
          this.getData5();
          break;
        case 6:
          this.getData6();
          break;
        default:

      }
    },
    getData0(){
      let p = {
        "dtid": this.id
      };
      this.$api.post('/refugeesWarningController/getPermanentResidenceParInfo', p,
        r => {
          this.xqData0=r.data
        })
    },
    getData1(){
      let p = {
        "rybh": this.id
      };
      this.$api.post('/refugeesWarningController/getMovementRecordParInfo', p,
        r => {
          this.xqData1=r.data
        })
    },
    getData2(){
      let p = {
        "dtid": this.id
      };
      this.$api.post('/educationParController/getJZJXXParInfo', p,
        r => {
          this.xqData2=r.data
        })
    },
    getData3(){
      let p = {
        "cjbh": this.id
      };
      this.$api.post('/educationParController/getJZCJXXParInfo', p,
        r => {
          this.xqData3=r.data
        })
    },
    getData4(){
      let p = {
        "jlbh": this.id
      };
      this.$api.post('/educationParController/getSJXXParInfo', p,
        r => {
          this.xqData3=r.data
        })
    },
    getData6(){
      let p = {
        "dtid": this.id
      };
      this.$api.post('/wadeCouldWarningController/getESLZLZXXParInfo', p,
        r => {
          this.xqData6=r.data
        })
    },
  }
}
</script>

